import { BiPlugin } from '../../types';
import toolbar from './toolbar';
import getFonts from './queries/getFonts';
import * as actions from './actions';
import onAction from './handlers/onAction';
import createRenderToolbar from './createRenderToolbar';
import renderEditable from './renderEditable';

const queries = {
  getFonts,
};

function createFontsPlugin(config): BiPlugin {
  const fontsConfig = config.fonts || {};
  return {
    toolbar: toolbar(fontsConfig),
    renderToolbar: createRenderToolbar(fontsConfig.locale),
    renderEditable,
    queries,
    onAction,
    models: {
      klass: 'mark',
      type: 'fonts',
    },
  };
}

createFontsPlugin.actions = actions;
createFontsPlugin.queries = queries;

export default createFontsPlugin;
